import './styles/app.scss';

import * as React from "react";
import * as ReactDOM from "react-dom";

interface AppProps {
  name: string
}

const App2 = (props: AppProps) => <h1>{props.name}</h1>;

class App extends React.Component<AppProps, undefined> {
  render() {
    const { name } = this.props;

    return (
      <div>
        <button type="button" className="pt-button pt-icon-add">Default button</button>
        <button type="button" className="pt-button pt-icon-refresh"></button>
        <button type="button" className="pt-button pt-large pt-icon-add">Large button</button>
        <button type="button" className="pt-button pt-large pt-icon-refresh"></button>
        <h1>{name}</h1>
      </div>
    );
  }
}

ReactDOM.render(<App name="My World!!" />, document.getElementById("root"));



